import React, { useMemo, useCallback } from 'react';
//antd
import { Button, Typography, Dropdown, Menu, Tag } from 'antd';
import { EllipsisOutlined } from '@ant-design/icons';
//lib
import i18n from '../../../../libs/i18n';
//utils
import { round } from '../../../../utils/number';

const { Text } = Typography;

const useTable = ({ current_direction, handleRemoveOrder }) => {
    const truck_columns = useMemo(() => ([
        {
            title: 'Bin s',
            dataIndex: 'license_plates',
            render: (text) => (
                <Text>{i18n.t(text)}</Text>
            )
        },
        {
            title: 'Loi xe',
            dataIndex: 'type',
            render: (text) => (
                <Text>{i18n.t(text)}</Text>
            )
        },
        {
            title: 'Ti trng',
            dataIndex: 'weight',
            render: (text) => (
                <Text>{text + ' kg'}</Text>
            )
        },
        {
            title: 'Kch thc',
            dataIndex: 'size',
            render: (text) => (
                <Text>{text + ' khi'}</Text>
            )
        },
        {
            title: 'Ti trng vt qu',
            dataIndex: 'max_weight',
            render: (text) => (
                <Text>{text + ' kg'}</Text>
            )
        },
    ]), []);

    const address_columns = useMemo(() => ([
        {
            title: 'STT',
            dataIndex: 'STT'
        },
        {
            title: 'Tn ng',
            dataIndex: 'address'
        },
        {
            title: '',
            dataIndex: 'operation',
            className: "antd-custom-action-table",
            width: 50,
            render: (text, item) => (
                <Button type='link' danger>{"Xa"}</Button>
            ),
        }
    ]), []);

    const info_columns = useMemo(() => ([
        {
            title: 'S n hng',
            dataIndex: 'number_order',
        },
        {
            title: 'Tng trng lng',
            dataIndex: 'weight',
            render: text => (
                <Text>{round(text)}</Text>
            )
        },
        {
            title: 'Tng kch thc',
            dataIndex: 'size',
            render: text => (
                <Text>{round(text)}</Text>
            )
        }
    ]), []);

    const action_menu = useCallback((order) => {
        return (
            <Menu>
                <Menu.Item>
                    <span>{"Thng tin n hng"}</span>
                </Menu.Item>
                <Menu.Item onClick={() => handleRemoveOrder({ order, current_direction })}>
                    <span>{"G khi tuyn"}</span>
                </Menu.Item>
            </Menu>
        );
    }, [current_direction, handleRemoveOrder]);

    const order_columns = useMemo(() => ([
        {
            title: 'a ch',
            dataIndex: 'address',
        },
        {
            title: 'Chi nhnh',
            dataIndex: 'agency',
        },
        {
            title: 'M n hng',
            dataIndex: 'order_id'
        },
        {
            title: 'Tng s kin hng',
            dataIndex: 'quantity'
        },
        {
            title: 'Tng trng lng',
            dataIndex: 'weight',
            render: (text) => (
                <Text>{text + ' Kg'}</Text>
            )
        },
        {
            title: 'Kch thc',
            dataIndex: 'size',
            render: (text) => (
                <Text>{text + ' Khi'}</Text>
            )
        },
        {
            title: 'Cnh bo',
            dataIndex: 'warning',
            render: text => {
                return (
                    <>
                        {
                            text ? <Tag color='warning'>{text}</Tag> : <span></span>
                        }
                    </>
                )
            }
        },
        {
            title: '',
            dataIndex: 'operation',
            className: "antd-custom-action-table",
            width: 50,
            render: (text, item) => (
                <div>
                    <Dropdown overlay={action_menu(item)} trigger={['click']}>
                        <EllipsisOutlined rotate={90} onClick={e => e.preventDefault()} />
                    </Dropdown>
                </div>
            ),
        },
    ]), [action_menu])


    return {
        truck_columns,
        address_columns,
        order_columns,
        info_columns
    }
}

export default useTable;