import React, { useMemo, useCallback } from 'react';
//antd
import { Button, Typography, Dropdown, Menu, Tag } from 'antd';
import { EllipsisOutlined } from '@ant-design/icons';
//lib
import i18n from '../../../../libs/i18n';
//utils
import { round } from '../../../../utils/number';
//actions
import actions from './actions';

const { Text } = Typography;

const useTable = ({ dispatchState }) => {
    const goToChangeTruck = useCallback(() => {
        dispatchState({
            type: actions.SET_STEP,
            step: 1
        })
    }, [dispatchState])

    const truck_columns = useMemo(() => ([
        {
            title: 'Bin s',
            dataIndex: 'license_plates',
            render: (text) => (
                <Text>{i18n.t(text)}</Text>
            )
        },
        {
            title: 'Loi xe',
            dataIndex: 'type',
            render: (text) => (
                <Text>{i18n.t(text)}</Text>
            )
        },
        {
            title: 'Ti trng',
            dataIndex: 'weight',
            render: (text) => (
                <Text>{text + ' kg'}</Text>
            )
        },
        {
            title: 'Kch thc',
            dataIndex: 'size',
            render: (text) => (
                <Text>{text + ' khi'}</Text>
            )
        },
        {
            title: 'Ti trng vt qu',
            dataIndex: 'max_weight',
            render: (text) => (
                <Text>{text + ' kg'}</Text>
            )
        },
        {
            dataIndex: 'operator',
            render: () => (
                <Button type='link' onClick={goToChangeTruck}>{"i xe"}</Button>
            )
        }
    ]), [goToChangeTruck]);

    const info_columns = useMemo(() => ([
        {
            title: 'S n hng',
            dataIndex: 'number_order',
        },
        {
            title: 'Tng trng lng',
            dataIndex: 'weight',
            render: text => (
                <Text>{round(text)}</Text>
            )
        },
        {
            title: 'Tng kch thc',
            dataIndex: 'size',
            render: text => (
                <Text>{round(text)}</Text>
            )
        }
    ]), []);

    const action_menu = useCallback((order) => {
        return (
            <Menu>
                <Menu.Item>
                    <span>{"Thng tin n hng"}</span>
                </Menu.Item>
                <Menu.Item >
                    <span>{"G khi tuyn"}</span>
                </Menu.Item>
            </Menu>
        );
    }, []);

    const order_columns = useMemo(() => ([
        {
            title: 'a ch',
            dataIndex: 'address',
        },
        {
            title: 'Chi nhnh',
            dataIndex: 'agency',
        },
        {
            title: 'M n hng',
            dataIndex: 'order_id'
        },
        {
            title: 'Tng s kin hng',
            dataIndex: 'quantity'
        },
        {
            title: 'Tng trng lng',
            dataIndex: 'weight',
            render: (text) => (
                <Text>{text + ' Kg'}</Text>
            )
        },
        {
            title: 'Kch thc',
            dataIndex: 'size',
            render: (text) => (
                <Text>{text + ' Khi'}</Text>
            )
        },
        {
            title: 'Cnh bo',
            dataIndex: 'warning',
            render: text => {
                return (
                    <>
                        {
                            text ? <Tag color='warning'>{text}</Tag> : <span></span>
                        }
                    </>
                )
            }
        },
        {
            title: '',
            dataIndex: 'operation',
            className: "antd-custom-action-table",
            width: 50,
            render: (text, item) => (
                <div>
                    <Dropdown overlay={action_menu(item)} trigger={['click']}>
                        <EllipsisOutlined rotate={90} onClick={e => e.preventDefault()} />
                    </Dropdown>
                </div>
            ),
        },
    ]), [action_menu])


    return {
        truck_columns,
        order_columns,
        info_columns
    }
}

export default useTable;